@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=ABeeZee&display=swap);
:root {
  --red: #ed5a5b;
  --white: #ffffff;
  --dark: rgba(46, 46, 46, 1);
  --dark-90: rgba(46, 46, 46, 0.9);
  --color-transition: color ease 0.3s;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0%;
  }
  100% {
    opacity: 100%;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0%;
  }
  100% {
    opacity: 100%;
  }
}

* {
  box-sizing: border-box; /* padding and border included within width, height */
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

body,
html {
  height: 100%;
  overflow: hidden; /* might change later when dealing w/ responsiveness */
}
body {
  font-family: "Lato", sans-serif;
  font-size: 18px;
  color: #ffffff;
  color: var(--white);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(rgba(46, 46, 46, 0.9), rgba(46, 46, 46, 0.9)),
    url("/static/media/BG Pic.07be3385.png") no-repeat;
  background: linear-gradient(var(--dark-90), var(--dark-90)),
    url("/static/media/BG Pic.07be3385.png") no-repeat;
  background-size: cover;
  -webkit-animation: fadeIn 2s ease;
          animation: fadeIn 2s ease;
}

a {
  text-decoration: none;
  color: #ffffff;
  color: var(--white);
}

.App {
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-columns: minmax(60px, 1fr) repeat(6, minmax(50px, 190px)) minmax(
      60px,
      1fr
    );
  grid-template-rows: 120px 1fr 100px;
  grid-column-gap: 30px;
  -webkit-column-gap: 30px;
          column-gap: 30px;
}

.ContentContainer {
  overflow: hidden;
  grid-column: 2 / span 6;
  grid-row: 2 / 3;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

/* Hide scrollbar for Chrome, Safari and Opera */
.ContentContainer::-webkit-scrollbar {
  display: none;
}

#Home {
  height: 100%;
  margin-top: 10px;
  display: grid;
  grid-template-columns: subgrid;
}

#Home p {
  font-family: "ABeeZee", sans-serif;
}

#Home img {
  height: 400px;
  grid-column-end: 8;
  justify-self: end;
}

.SideNav {
  grid-column: 8 / 9;
  grid-row: 2 / 3;
  align-self: center;
  justify-self: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.arrow,
.circle {
  margin-bottom: 5px;
}

.active {
  color: var(--red);
}

.circle {
  font-size: 10px;
}

.arrow:hover {
  transition: var(--color-transition);
  color: var(--red);
}

.SideNav a {
  scroll-behavior: smooth;
}

#nametag {
  font-weight: 700;
  font-size: 24px;
}

.Header {
  align-self: center;
  grid-column: 2 / span 6;
  grid-row: 1 / 2;
}

nav {
  display: flex;
  align-items: center;
}

.links {
  margin-left: auto;
}

.links a {
  margin-left: 50px;
}

.Footer {
  grid-column: 2 / span 6;
  grid-row: 3 / span 1;
  align-self: center;
}

.footerIcon {
  margin-right: 50px;
}

.footerIcon:hover {
  transition: var(--color-transition);
  color: var(--red);
}

#About {
  height: 100%;
  margin-top: 10px;
  display: grid;
  grid-template-columns: subgrid;
}

#About p {
  font-family: "ABeeZee", sans-serif;
}

.ResumeContainer {
  grid-column-end: 8;
  justify-self: end;
}

.ResumeContainer a {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ResumeContainer img {
  height: 400px;
}

#Projects {
  height: 100%;
  margin-top: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 30px;
  -webkit-column-gap: 30px;
          column-gap: 30px;
}

.ProjectContainer {
  justify-self: stretch;
  grid-column: auto / span 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ProjectBlock {
  background-color: white;
  width: 100%;
  height: 100%;
}

#block-1 {
  background-image: url(/static/media/acnh-1.fb1e3467.gif);
}

#Contact {
  margin-top: 10px;
  height: 100%;
  margin-top: 10px;
}

.ContactContainer {
  background-color: white;
  height: 80%;
  margin-top: 20px;
}

